<template>
<v-app>
    <home-head></home-head>
    <course-summary :courses=courses></course-summary>
    <v-row no-gutters dense>
      <v-col cols="1"></v-col>
      <v-col cols="5">
          <home-notice :notices=notices :title="notices_title" :route="notices_route"></home-notice>
      </v-col>
      <v-col cols="5">
          <home-notice :notices=roads :title="roads_title" :route="roads_route"></home-notice>
      </v-col>
      <v-col cols="1"></v-col>
    </v-row>
</v-app>

</template>

<script>
  import HomeHead from '../components/HomeHead';
  import CourseSummary from '../components/CourseSummary.vue';
  import HomeNotice from '../components/HomeNotice.vue';
  import datafile from '@/data/HomeData.js';
  export default {
    components: {
      HomeHead,HomeNotice,CourseSummary,
    },
    created () {
        this.$http.get('/api/notices')
        .then((response) => {
        this.notices = response.data;
        this.roads = response.data;
        })
    },
    data(){
      return{
        courses:datafile.courses,
        notices:[],
        roads:[],
        notices_title:"Groad 소식 및 행사",
        roads_title:"Groad 코스 안내",
        notices_route:"/community/notice",
        roads_route:"/community/sns"
      }
    }
  }

</script>
